"use client";

import * as React from "react";
import { format } from "date-fns";
import * as z from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm, useFieldArray } from "react-hook-form";

import type { Tournament, TournamentEntry, User, Partnership } from "@/lib/types";
import { events, partneredEvents } from "@/lib/events";
import { useAuth } from "@/contexts/auth-context";
import { useToast } from "@/hooks/use-toast";

import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { MultiSelect } from "@/components/ui/multi-select";
import { ExternalLink, PlusCircle, Trash2 } from "lucide-react";

interface TournamentSignupCardProps {
  tournament: Tournament;
  users: User[];
  onTournamentUpdate: (tournament: Tournament) => void;
}

const partnershipSchema = z.object({
  event: z.string().min(1, "Please select an event."),
  partnerIds: z.array(z.string()).min(1, "Please select at least one partner."),
});

const signupSchema = z.object({
  events: z.array(z.string()).min(1, "Please select at least one event.").max(3, "You can select up to 3 events."),
  partnerships: z.array(partnershipSchema),
});


export default function TournamentSignupCard({ tournament, users, onTournamentUpdate }: TournamentSignupCardProps) {
  const { user } = useAuth();
  const { toast } = useToast();
  const [open, setOpen] = React.useState(false);

  const userEntry = user ? tournament.entries.find(entry => entry.id === user.id) : undefined;

  const form = useForm<z.infer<typeof signupSchema>>({
    resolver: zodResolver(signupSchema),
    defaultValues: {
      events: userEntry?.events || [],
      partnerships: userEntry?.partnerships || [],
    },
  });
  
  React.useEffect(() => {
    if (open && userEntry) {
      form.reset({
        events: userEntry.events || [],
        partnerships: userEntry.partnerships || [],
      })
    } else if (open) {
      form.reset({
        events: [],
        partnerships: [],
      })
    }
  }, [open, userEntry, form]);


  const selectedEvents = form.watch("events");

  // Sync partnerships with selected partnered events
  React.useEffect(() => {
    const currentPartnerships = form.getValues('partnerships');
    const newPartnerships: Partnership[] = [];
    const partneredEventsSelected = selectedEvents.filter(e => partneredEvents.includes(e));

    partneredEventsSelected.forEach(event => {
      const existing = currentPartnerships.find(p => p.event === event);
      if (existing) {
        newPartnerships.push(existing);
      } else {
        newPartnerships.push({ event, partnerIds: [] });
      }
    });

    form.setValue('partnerships', newPartnerships);

  }, [selectedEvents, form]);


  function onSubmit(values: z.infer<typeof signupSchema>) {
    if (!user) return;
    
    // Validate partnerships
    for (const partnership of values.partnerships) {
      if (partnership.partnerIds.length === 0) {
        toast({ variant: 'destructive', title: 'Partner Required', description: `Please select a partner for ${partnership.event}.`});
        return;
      }
    }

    const partnershipsWithNames = values.partnerships.map(p => ({
        ...p,
        partnerNames: p.partnerIds.map(id => users.find(u => u.id === id)?.name || '')
    }));

    const newEntry: TournamentEntry = {
      id: user.id,
      name: user.name,
      events: values.events,
      partnerships: partnershipsWithNames
    };

    const updatedEntries = tournament.entries.filter(entry => entry.id !== user.id).concat(newEntry);
    onTournamentUpdate({ ...tournament, entries: updatedEntries });
    
    toast({ title: "Successfully Signed Up!", description: `Your entry for ${tournament.name} has been updated.` });
    setOpen(false);
  }

  const partnerships = form.watch('partnerships');

  return (
    <Card>
      <CardHeader>
        <CardTitle className="font-headline">{tournament.name}</CardTitle>
        <CardDescription>
          {format(new Date(tournament.date), "EEEE, MMMM d, yyyy")}
        </CardDescription>
      </CardHeader>
      <CardContent className="flex flex-col gap-2">
         {tournament.webpageUrl && (
              <Button variant="outline" size="sm" asChild>
                <a href={tournament.webpageUrl} target="_blank" rel="noopener noreferrer">
                  <ExternalLink className="mr-2 h-3 w-3"/> Tournament Webpage
                </a>
              </Button>
            )}
            {tournament.scheduleUrl && (
              <Button variant="outline" size="sm" asChild>
                <a href={tournament.scheduleUrl} target="_blank" rel="noopener noreferrer">
                 <ExternalLink className="mr-2 h-3 w-3"/> Tournament Schedule
                </a>
              </Button>
            )}
      </CardContent>
      <CardFooter>
        <Dialog open={open} onOpenChange={setOpen}>
          <DialogTrigger asChild>
            <Button className="w-full">
              {userEntry ? 'Update Entry' : 'Sign Up'}
            </Button>
          </DialogTrigger>
          <DialogContent className="max-w-2xl">
            <DialogHeader>
              <DialogTitle>Sign Up for {tournament.name}</DialogTitle>
              <DialogDescription>
                Select up to 3 events. Partnered events require a partner.
              </DialogDescription>
            </DialogHeader>
            <Form {...form}>
              <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6 py-4">
                <FormField
                  control={form.control}
                  name="events"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Events</FormLabel>
                      <MultiSelect
                        options={events.map(e => ({ label: e, value: e }))}
                        onValueChange={field.onChange}
                        defaultValue={field.value}
                        placeholder="Select up to 3 events..."
                        maxCount={3}
                      />
                      <FormMessage />
                    </FormItem>
                  )}
                />
                
                {partnerships && partnerships.length > 0 && (
                  <div className="space-y-4 rounded-md border p-4">
                    <h4 className="font-medium">Partnered Events</h4>
                     {partnerships.map((partnership, index) => {
                       const isWsd = partnership.event === "World Schools Debate (WSD)";
                       return (
                         <FormField
                            key={partnership.event}
                            control={form.control}
                            name={`partnerships.${index}.partnerIds`}
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>{partnership.event}</FormLabel>
                                    {isWsd ? (
                                        <MultiSelect
                                            options={users.filter(u => u.id !== user?.id).map(u => ({label: u.name, value: u.id}))}
                                            onValueChange={field.onChange}
                                            defaultValue={field.value}
                                            placeholder="Select up to 4 teammates..."
                                            maxCount={4}
                                        />
                                    ) : (
                                        <Select onValueChange={(value) => field.onChange([value])} defaultValue={field.value?.[0]}>
                                            <FormControl>
                                                <SelectTrigger>
                                                <SelectValue placeholder="Select a partner" />
                                                </SelectTrigger>
                                            </FormControl>
                                            <SelectContent>
                                                {users.filter(u => u.id !== user?.id).map(u => (
                                                <SelectItem key={u.id} value={u.id}>
                                                    {u.name}
                                                </SelectItem>
                                                ))}
                                            </SelectContent>
                                        </Select>
                                    )}
                                    <FormMessage />
                                </FormItem>
                            )}
                          />
                       )
                     })}
                  </div>
                )}
                <DialogFooter>
                  <Button type="submit">Submit Entry</Button>
                </DialogFooter>
              </form>
            </Form>
          </DialogContent>
        </Dialog>
      </CardFooter>
    </Card>
  );
}
